@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Mukta:wght@400;700&display=swap");
body {
  font-family: "Mukta", sans-serif;
  font-size: 14px;
  font-weight: 400;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input.active.focus, input.active:focus,
input.focus, input:active.focus,
input:active:focus, input:focus {
  outline: none;
}

.titleStyle {
  font-size: 24px;
  letter-spacing: 2px;
}
@media screen and (max-width: 991px) {
  .titleStyle {
    font-size: 23px;
  }
}
@media screen and (max-width: 767px) {
  .titleStyle {
    font-size: 22px;
  }
}
@media screen and (max-width: 575px) {
  .titleStyle {
    font-size: 20px;
  }
}

.textStyle {
  font-size: 22px;
  padding: 35px 0;
  letter-spacing: 2px;
}
@media screen and (max-width: 991px) {
  .textStyle {
    font-size: 20px;
  }
}
@media screen and (max-width: 767px) {
  .textStyle {
    font-size: 18px;
  }
}
@media screen and (max-width: 575px) {
  .textStyle {
    font-size: 16px;
  }
}

.buttonStyle {
  padding: 18px 70px;
  border: none;
  border-radius: 51px;
  font-size: 16px;
  letter-spacing: 2px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  transition: 0.3s transform linear;
}
.buttonStyle:hover {
  transform: translate3d(0, -10px, 0);
  transition: transform 0.3s linear;
  cursor: pointer;
}
.buttonStyle:focus {
  outline: none;
}
@media screen and (max-width: 767px) {
  .buttonStyle {
    font-size: 14px;
  }
}
@media screen and (max-width: 575px) {
  .buttonStyle {
    font-size: 13px;
  }
}
@media screen and (max-width: 425px) {
  .buttonStyle {
    font-size: 12px;
  }
}

/*header*/
#header {
  padding-top: 20px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
#header .Logo {
  font-size: 48px;
  color: #ffffff;
  font-family: "Dancing Script", cursive;
}
@media screen and (max-width: 991px) {
  #header .Logo {
    font-size: 44px;
  }
}
#header .headerNav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#header .headerNav .nav li a {
  position: relative;
  display: block;
  margin: 0 10px;
  padding: 5px 10px;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  overflow: hidden;
}
#header .headerNav .nav li a:before {
  content: "";
  position: absolute;
  left: -100%;
  width: 100%;
  height: 4px;
  background: #A102D1;
  transition: 0.5s;
}
#header .headerNav .nav li a:hover {
  color: #fff;
}
@media screen and (max-width: 1199px) {
  #header .headerNav .nav li a {
    font-size: 15px;
    margin: 0 5px;
  }
}
@media screen and (max-width: 991px) {
  #header .headerNav .nav li a {
    display: none;
  }
}
#header .headerNav .nav li a:hover::before {
  animation: animate 0.5s linear forwards;
}
@keyframes animate {
  0% {
    top: calc(50% - 2px);
    left: -100%;
    height: 4px;
    z-index: 1;
  }
  50% {
    top: calc(50% - 2px);
    left: 0;
    height: 4px;
    z-index: 1;
  }
  100% {
    top: 0;
    left: 0;
    height: 100%;
    z-index: -1;
  }
}
#header .headerNav .searchStyle {
  width: 235px;
  color: #363849;
  font-size: 13px;
  padding: 7px 10px;
  margin-left: 20px;
  border: 1px #363849 solid;
  border-radius: 50px;
  letter-spacing: 1px;
}
#header .headerNav .searchStyle:focus {
  color: #363849;
  letter-spacing: 1px;
}
@media screen and (max-width: 991px) {
  #header .headerNav .searchStyle {
    display: none;
  }
}
#header .hamburger-menu {
  display: none;
}
@media screen and (max-width: 991px) {
  #header .hamburger-menu {
    display: block;
  }
  #header .hamburger-menu #menu__toggle {
    opacity: 0;
  }
  #header .hamburger-menu #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }
  #header .hamburger-menu #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #header .hamburger-menu #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #header .hamburger-menu #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    right: 0;
  }
  #header .hamburger-menu .menu__btn {
    display: flex;
    align-items: center;
    position: absolute;
    top: 11px;
    right: 0;
    width: 47px;
    cursor: pointer;
    z-index: 1;
  }
}
@media screen and (max-width: 991px) and (max-width: 575px) {
  #header .hamburger-menu .menu__btn {
    right: 15px;
  }
}
@media screen and (max-width: 991px) and (max-width: 425px) {
  #header .hamburger-menu .menu__btn {
    width: 40px;
  }
}
@media screen and (max-width: 991px) {
  #header .hamburger-menu .menu__btn > span,
  #header .hamburger-menu .menu__btn > span::before,
  #header .hamburger-menu .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 5px;
    background-color: #ffffff;
    transition-duration: 0.25s;
  }
}
@media screen and (max-width: 991px) and (max-width: 425px) {
  #header .hamburger-menu .menu__btn > span,
  #header .hamburger-menu .menu__btn > span::before,
  #header .hamburger-menu .menu__btn > span::after {
    height: 3px;
  }
}
@media screen and (max-width: 991px) {
  #header .hamburger-menu .menu__btn > span::before {
    content: "";
    top: -12px;
  }
}
@media screen and (max-width: 991px) and (max-width: 425px) {
  #header .hamburger-menu .menu__btn > span::before {
    top: -10px;
  }
}
@media screen and (max-width: 991px) {
  #header .hamburger-menu .menu__btn > span::after {
    content: "";
    top: 12px;
  }
}
@media screen and (max-width: 991px) and (max-width: 425px) {
  #header .hamburger-menu .menu__btn > span::after {
    top: 10px;
  }
}
@media screen and (max-width: 991px) {
  #header .hamburger-menu .menu__box {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 80px 40px;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 500px;
    height: 100vh;
    margin: 0;
    list-style: none;
    background-color: rgba(0, 0, 0, 0.9);
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
    transition-duration: 0.25s;
  }
  #header .hamburger-menu .menu__box .menu__item {
    display: block;
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    padding: 10px 15px;
    transition: border 0.2s linear;
  }
  #header .hamburger-menu .menu__box .menu__item:hover {
    background-color: transparent;
    border: 2px #ffffff solid;
    transition: border 0.2s linear;
  }
}
@media screen and (max-width: 991px) and (max-width: 767px) {
  #header .hamburger-menu .menu__box .menu__item {
    font-size: 22px;
    text-align: center;
  }
}
@media screen and (max-width: 991px) and (max-width: 575px) {
  #header .hamburger-menu .menu__box .menu__item {
    font-size: 20px;
  }
}
@media screen and (max-width: 991px) and (max-width: 767px) {
  #header .hamburger-menu .menu__box {
    width: 100%;
  }
}

